.card {

    background: #333;
    border-radius: 4px;
    overflow: hidden;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.05);
}



.card.is-loading .image {
    background: #000;
    width: 100%;
    height: 100%;
    background: linear-gradient(110deg, #333 8%, #2c2c2c 18%, #333 33%);
    border-radius: 5px;
    background-size: 200% 100%;
    animation: 1.5s shine linear infinite;
}



@keyframes shine {
    to {
        background-position-x: -200%;
    }
}