.list-card .card-movie {
    margin-bottom: 24px;
    width: calc(12.5% - 24px) !important
}

@media (max-width: 1025px) {
    .list-card .card-movie {
        width: calc(20% - 24px) !important
    }
}

@media (max-width: 769px) {
    .list-card .card-movie {
        width: calc(25% - 24px) !important
    }
}


@media (max-width: 650px) {
    .list-card .card-movie {
        width: calc(50% - 24px) !important
    }
}


