.card-information .gender {
    padding-right: 16px;
    position: relative;

}

.card-information .gender:last-child:after {
    display: none;

}

.card-information .gender:after {
    content: ' • ';
    top: 0px;
    right: 5px;
    font-size: 16px;
    position: absolute;
    color: white;

}


.add-lists:hover {
    transition: 0.3s ease;
    background: #bdbdbd !important;
}

.list-card-similar .card-movie {
    margin-bottom: 24px;
    width: calc(25% - 24px) !important
}

.imagen-information-animation {
    background: linear-gradient(110deg, #333 8%, #2c2c2c 18%, #333 33%);
    background-size: 200% 100%;
    animation: 1.5s shine linear infinite;
}

@keyframes shine {
    to {
        background-position-x: -200%;
    }
}

.list-card-similar .card-movie {
    background: linear-gradient(110deg, #333 8%, #2c2c2c 18%, #333 33%);
    background-size: 200% 100%;
    min-height: 200px;
    max-height: 205px;
    animation: 1.5s shine linear infinite;
}


@media (max-width: 1025px) {
    .list-card-similar .card-movie {
        width: calc(25% - 24px) !important
    }
}



@media (max-width: 650px) {
    .list-card-similar .card-movie {
        width: calc(33.333% - 24px) !important;
        min-height: 110px;
        max-height: 110px;
    }
}