.slider-movies .slick-dots {
    position: absolute !important;
    bottom: inherit;
    top: -31px;
    right: 0;
    width: auto;
}

.slider-movies .slick-dots li {
    height: 2px;
    width: 12px;
}

.slider-movies .slick-dots li button {
    height: 2px;
    width: 12px;
}

.slider-movies .slick-dots li button:before {
    content: '';
    background: #4d4d4d;
    height: 2px;
    margin-left: 1px;
    width: 12px;
    opacity: 0.25;
}

.slider-movies .slick-dots li.slick-active button:before {
    opacity: 1;
    background: #aaa;
}

@media (max-width: 1025px) {
    .slider-movies .slick-arrow {
        display: none !important
    }
}


.slider-movies .slick-prev {
    background: #00000050;
    width: 53px;
    height: 100%;
    left: 0px;
    top: 150px;
    z-index: 5;
}

.slider-movies .slick-next {
    background: #00000050;
    width: 53px;
    height: 100%;
    right: -24px;
    top: 150px;
    z-index: 5;
}

.slider-movies .slick-prev:before {
    content: '‹';
    font-weight: bold;
    font-size: 70px;
}

.slider-movies .slick-next:before {
    content: '›';
    font-weight: bold;
    font-size: 70px;
}

.slider-movies .slick-list {
    overflow: inherit
}


.slider-movies .card-movie {
    background: linear-gradient(110deg, #333 8%, #2c2c2c 18%, #333 33%);
    background-size: 200% 100%;
    animation: 1.5s shine linear infinite;
    min-height: 300px;
    max-height: 300px;

}


@keyframes shine {
    to {
        background-position-x: -200%;
    }
}