.card-movie .more-information {
    opacity: 0;
    background: #141414;
    display: flex;
    position: absolute;
    transition: .3s ease;
    height: 200px;
    width: auto;
    z-index: 20;
    box-shadow: rgb(0 0 0 / 75%) 0px 3px 10px;
    pointer-events: none;
    border-radius: 4px;
    transform: scale(1, 1);

    transform-origin: center;
}

.card-movie .more-information.active {
    opacity: 1;
    transition: .3s ease;
    pointer-events: all;
    transform: scale(1.4, 1.4);
    transform-origin: center;
}


.card-movie .more-information .gender {
    padding-right: 16px;
    position: relative;

}

.card-movie .more-information .gender:last-child:after {
    display: none;

}

.card-movie .more-information .gender:after {
    content: ' • ';
    top: -2px;
    right: 5px;
    font-size: 16px;
    position: absolute;
    color: white;

}

.add-list::before {
    content: '';
    position: absolute;
    bottom: -8px;
    left: 43%;
    width: 0;
    height: 0;
    border-left: 8px solid transparent;
    border-right: 8px solid transparent;
    border-top: 16px solid #fff;
}


.card-poster {
    background: linear-gradient(110deg, #333 8%, #2c2c2c 18%, #333 33%);
    background-size: 200% 100%;
    animation: 1.5s shine linear infinite;

}

@keyframes shine {
    to {
        background-position-x: -200%;
    }
}